				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="dianhua"
					label="">
					<template #default="scope">
						{{scope.row.dianhua}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
