					label="">
					<template #default="scope">
						{{scope.row.qita}}
					</template>
				</el-table-column>
				<el-table-column prop="ispay" label="" min-width="140" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<span style="margin-right:10px">{{scope.row.ispay==''?'':''}}</span>
						<el-button v-if="scope.row.ispay!='' && btnAuth('chufangxinxi','')" size="small" @click.stop="payClick(scope.row)"></el-button>
					</template>
				</el-table-column>
				<el-table-column label="" width="300" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<el-button class="view_btn" type="info" v-if=" btnAuth('chufangxinxi','')" @click="infoClick(scope.row.id)">
							<i class="iconfont icon-sousuo2"></i>
							
						</el-button>
						<el-button class="edit_btn" type="primary" @click="editClick(scope.row.id)" v-if=" btnAuth('chufangxinxi','')">
							<i class="iconfont icon-xiugai5"></i>
													</el-button>
						<el-button class="del_btn" type="danger" @click="delClick(scope.row.id)"  v-if="btnAuth('chufangxinxi','')">
							<i class="iconfont icon-shanchu4"></i>
													</el-button>
					</template>
				</el-table-column>
			</el-table>
			<el-pagination
				background
				:layout="layouts.join(',')"
				:total="total"
				:page-size="listQuery.limit"
                v-model:current-page="listQuery.page"
				prev-text=""
				next-text=""
				:hide-on-single-page="false"
				:style='{}'
				:page-sizes="[10, 20, 30, 40, 50, 100]"
				@size-change="sizeChange"
				@current-change="currentChange"  />
		</div>
		<formModel ref="formRef" @formModelChange="formModelChange"></formModel>
		<payForm ref="pay" @payChange="searchClick()"></payForm>
		<!--  -->
		<el-dialog v-model="echartVisible" title="" width="70%">
            <div  id="xujiaojineEchart1" style="width:100%;height:600px;"></div>
			<template #footer>
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="echartVisible=false"></el-button>
				</span>
			</template>
		</el-dialog>
