<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="50%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row >
					<el-col :span="24">
						<el-form-item label="" prop="yishengzhanghao">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.yishengzhanghao?true:false"
								v-model="form.yishengzhanghao" 
								placeholder=""
								@change="yishengzhanghaoChange">
								<el-option v-for="(item,index) in yishengzhanghaoLists" :label="item" :value="item">
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="yishengxingming">
							<el-input class="list_inp" v-model="form.yishengxingming" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.yishengxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="banci">
							<el-input class="list_inp" v-model="form.banci" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.banci?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="shangbanshijian">
							<el-date-picker
								class="list_date"
								v-model="form.shangbanshijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:disabled-date="shangbanshijianDisabledDate"
								:readonly="!isAdd||disabledForm.shangbanshijian?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="\" prop="shangbanshizhang">
							<el-input class="list_inp" v-model.number="form.shangbanshizhang" placeholder="\"
                                type="text"
								:readonly="!isAdd||disabledForm.shangbanshizhang?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="xiujiashijian">
							<el-date-picker
								class="list_date"
								v-model="form.xiujiashijian"
								format="YYYY  MM  DD "
								value-format="YYYY-MM-DD"
								type="datetime"
								:disabled-date="xiujiashijianDisabledDate"
								:readonly="!isAdd||disabledForm.xiujiashijian?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="fabushijian">
							<el-date-picker
								class="list_date"
								v-model="form.fabushijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.fabushijian?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="beizhu">
							<editor :value="form.beizhu" placeholder="" :readonly="!isAdd||disabledForm.beizhu?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'beizhu')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'paibanxinxi'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
        yishengzhanghao : false,
        yishengxingming : false,
        banci : false,
        shangbanshijian : false,
        shangbanshizhang : false,
        xiujiashijian : false,
        fabushijian : false,
        beizhu : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
    
	const rules = ref({
		yishengzhanghao: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		yishengxingming: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		banci: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		shangbanshijian: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		shangbanshizhang: [
			{required: true,message: '',trigger: 'blur'}, 
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		xiujiashijian: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		fabushijian: [
		],
		beizhu: [
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const yishengzhanghaoLists = ref([])
	// 
	const shangbanshijianDisabledDate = (date) => {
		return date.getTime() < Date.now();
	}
	// 
	const xiujiashijianDisabledDate = (date) => {
		return date.getTime() < Date.now();
	}

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			yishengzhanghao: '',
			yishengxingming: '',
			banci: '',
			shangbanshijian: '',
			shangbanshizhang: '',
			xiujiashijian: '',
			fabushijian: '',
			beizhu: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.beizhu = res.data.data.beizhu?(res.data.data.beizhu.replace(reg,'../../../cl515882190/file')):'';
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.fabushijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='yishengzhanghao'){
					form.value.yishengzhanghao = row[x];
					disabledForm.value.yishengzhanghao = true;
					continue;
				}
				if(x=='yishengxingming'){
					form.value.yishengxingming = row[x];
					disabledForm.value.yishengxingming = true;
					continue;
				}
				if(x=='banci'){
					form.value.banci = row[x];
					disabledForm.value.banci = true;
					continue;
				}
				if(x=='shangbanshijian'){
					form.value.shangbanshijian = row[x];
					disabledForm.value.shangbanshijian = true;
					continue;
				}
				if(x=='shangbanshizhang'){
					form.value.shangbanshizhang = row[x];
					disabledForm.value.shangbanshizhang = true;
					continue;
				}
				if(x=='xiujiashijian'){
					form.value.xiujiashijian = row[x];
					disabledForm.value.xiujiashijian = true;
					continue;
				}
				if(x=='fabushijian'){
					form.value.fabushijian = row[x];
					disabledForm.value.fabushijian = true;
					continue;
				}
				if(x=='beizhu'){
					form.value.beizhu = row[x];
					disabledForm.value.beizhu = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
		})
		context?.$http({
			url: `option/yisheng/yishengzhanghao`,
			method: 'get'
		}).then(res=>{
			yishengzhanghaoLists.value = res.data.data
		})
		//
		disabledForm.value.yishengxingming = true;
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	const yishengzhanghaoChange=()=>{
		context?.$http({
			url: `follow/yisheng/yishengzhanghao?columnValue=` + form.value.yishengzhanghao,
			method: 'get'
		}).then(res=>{
			if(res.data.data.yishengxingming){
				form.value.yishengxingming = res.data.data.yishengxingming
			}
		})
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save= async ()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success')
                        formVisible.value = false
					})
				}
			}else{
                context.$message.error('')
            }
		})
	}
	//
	const changeCrossData = async (row)=>{
        if(type.value == 'cross'){
            await context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {
				}
				//
				.list_date {
				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
				// 
				.list_editor {
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
